import {Settings} from './types';

/**
 * An implementation of the Frame-Stewart algorithm for calculating
 * the optimal moves given the number of disks and pegs.
 *
 * https://en.wikipedia.org/wiki/Tower_of_Hanoi#Frame%E2%80%93Stewart_algorithm
 */
const frameStewart = (disks: number, pegs: number) => {
    if (disks === 0) return 0;
    if (pegs > 1 && disks === 1) return 1;
    if (pegs === 3) return 2 ** disks - 1;
    if (pegs >= 3 && disks > 0) {
        let min = Infinity;
        for (let i = 1; i < disks; ++i) {
            const moves = 2 * frameStewart(i, pegs) + frameStewart(disks - i, pegs - 1);
            if (moves < min) min = moves;
        }
        return min;
    }
    return Infinity;
}

export const optimalMoves = (settings: Settings) => {
    if (settings.illegalMoves && !(settings.stacks < 3 && settings.disks > 1)) return settings.disks * 2 - 1;
    return frameStewart(settings.disks, settings.stacks);
};
