import {useEffect, useState} from 'react';
import {MS_IN_SECOND} from '../util/constants';

interface Props {
    startTime: number | null;
    endTime: number | null;
}

export const Timer = ({startTime, endTime}: Props) => {
    const [dummy, setDummy] = useState(0);

    useEffect(() => {
        if (endTime) return;

        // Force a re-render every 10 milliseconds.
        const interval = setInterval(() => setDummy(dummy + 1), 10);

        return () => clearInterval(interval);
    });

    if (startTime) {
        const remainingTime = ((endTime ?? Date.now()) - startTime) / MS_IN_SECOND;
        return <>{remainingTime.toFixed(3)} seconds</>;
    }

    return <>Not Started</>;
};
